export default {
	data() {
		return {
			userPhone: '',
			checkCode: '',
			disabled: false,
			content: '获取验证码',
			totalTime: 0,
		}
	},
	methods: {
		onChangePhone(v) {
			this.userPhone = v.detail
		},
		onChange(v) {
			this.checkCode = v.detail
		},
		getCode() {
			let ruleValue = this.$utils.checkPhone(this.userPhone)
			if(ruleValue) {
				this.$http.get({
					url: '/v1/rest/public/bgxsendcode',
					data:{
						phone: this.userPhone,
					}
				}).then(resp => {
					console.log(resp)
				})
				this.totalTime = 60
				this.timer = setInterval(() => {
					this.totalTime--
					this.content = this.totalTime + '秒后重试'
					this.disabled = true
					if(this.totalTime <= 0){
						this.content = '获取验证码'
						this.disabled = false
						clearInterval(this.timer)
					}
				},1000)
			}else{
				uni.showToast({
					title: '请输入有效的手机号码！',
					duration: 2000,
					icon: 'none'
				});
			}
		},
		handleBind() {
			let ruleValue = this.$utils.checkPhone(this.userPhone)
			if(!ruleValue){
				uni.showToast({
					title: '请输入有效的手机号码！',
					duration: 2000,
					icon: 'none'
				});
			}else if(this.checkCode == ''){
				uni.showToast({
					title: '请输入验证码！',
					duration: 2000,
					icon: 'none'
				});
			}else{
				uni.showModal({
					content: '绑定成功！',
					showCancel: false
				});
			}
		},
		// 微信登录
		getuserinfo(e){
			console.log(e)
		}
	}
}
